* {
  box-sizing: border-box;
  margin: 0px;
  color: rgb(20, 43, 111);
}
:root {
  --yellow: rgb(255, 217, 0);
  --blue: rgb(20, 43, 111);
}
body {
  margin: 0;
  font-family: LL Circular, -apple-system, BlinkMacSystemFont, Roboto,
    Helvetica Neue, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji,
    Segoe UI Symbol;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  text-align: left;
}
/* header */
/* menu  */
#menu-container {
  position: fixed;
  top: 0px;
  left: 0px;
  display: block;
  width: 100%;
  height: 100vh;
  z-index: 30;
  background-color: rgba(0, 0, 0, 0.5);
}
.menu33 {
  width: 33%;
  height: 100vh;
  background-color: white;
  float: left;
  z-index: 50;
  padding: 100px 50px 100px 100px;
  border-right: 0.1px solid rgba(128, 128, 128, 0.1);
  text-align: left;
}
#menu-container svg {
  position: relative;
  top: 5px;
  left: 0px;
}
#menu-container ul {
  margin: 0px 0px;
  padding: 18px 0px;
}
#menu-container ul li {
  list-style: none;
  display: block;
  margin: 13px 0px;
  font-size: 13px;
  color: rgb(20, 43, 111, 0.5);
  font-weight: 550;
}
#menu-container ul li a {
  cursor: pointer;
  font-size: 1.375rem;
  font-weight: 550;
}
#menu-container .menu-main li a {
  font-size: 1.875rem;
  line-height: 40px;
}
#menu-container .menu-notmain li a {
  font-size: 1.2rem;
  line-height: 20px;
  font-weight: 500;
}
#account {
  padding-left: 10px;
}
#menu-container a:hover {
  background-color: rgba(255, 217, 0, 0.5);
}
#menu-container .label {
  background-color: var(--yellow);
  position: relative;
  top: -5px;
  left: 0px;
}
#pregnancy ul,
#kids ul {
  padding: 5px 0px;
}
#pregnancy ul:nth-child(1),
#kids ul:nth-child(1) {
  margin-top: 13px;
}
/* menu  */
#menu {
  cursor: pointer;
  z-index: 100;
  float: left;
  font-size: 15px;
}
.closebtn {
  position: relative;
  top: -75px;
  right: -350px;
  cursor: pointer;
  height: 16px;
  width: 16px;
}
.closebtn:hover {
  opacity: 0.5;
}
#closearea {
  float: right;
  margin: 0px;
  width: 0%;
  height: 100vh;
  /* z-index: 10; */
}
/* menu end  */
/* navlinks  */
.navlinks {
  background-color: white;
  text-align: center;
  z-index: 1;
  width: 100%;
  height: 55px;
  position: fixed;
}
.nav-w {
  /* font-size: 1.725rem; */
  font-weight: 500;
  width: 75rem;
  margin-top: 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
#logo {
  position: absolute;
  top: 15px;
  left: 48%;
}
.navleft a {
  font-size: 15px;
  padding: 0px 20px;
}
/* Hero Section  */
#hero-section {
  border: 1px solid white;
  width: 100%;
}
#hero-img {
  margin: 50px 25px 60px 25px;
  height: 570px;
  display: flex;
  justify-content: center;
}
#p-rel {
  width: auto;
  position: absolute;
  top: 200px;
  text-align: center;
}
#p-rel div {
  display: flex;
  justify-content: center;
}
#hero-title > p {
  font-family: "LL Circular", sans-serif;
  font-size: 4.75rem;
  word-spacing: -5px;
  letter-spacing: -1px;
  line-height: 4.625rem;
  font-weight: 700;
  justify-content: center;
  align-items: center;
  width: 600px;
  padding-right: 150px;
  color: var(--blue);
}
#hero-title > p:nth-child(1) {
  text-align: right;
}
#hero-title > p:nth-child(2) {
  text-align: left;
  padding-bottom: 20px;
}
#hero-title em {
  font-size: 4.75rem;
  font-weight: 500;
}
#herobtn {
  font-family: "LL Circular", sans-serif;
  font-size: 1rem;
  line-height: 1.625rem;
  font-weight: 500;
  padding: 10px 50px;
  margin-top: 20px;
  background-color: var(--blue);
  border: 0.5px solid rgba(255, 255, 255, 0);
  color: white;
  border-radius: 25px;
  cursor: pointer;
}
#herobtn:hover {
  background-color: white;
  color: var(--blue);
}
/* Thumbnail cards */
.container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
.card {
  width: 270px;
  cursor: pointer;
  margin: 0px 15px;
  position: relative;
}
.card-img {
  width: 100%;
  height: 140px;
  object-fit: cover;
}
.card-text {
  width: 100%;
  background-color: rgba(230, 230, 230, 0.9);
  color: var(--blue);
  text-align: center;
  padding: 10px;
}
.label {
  margin: 0px 10px;
  padding: 7px 20px 7px 14px;
  font-size: 12px;
  font-weight: 600;
  background-color: rgba(255, 255, 255, 0.9);
  position: absolute;
  bottom: 55px;
  left: 5px;
}
/* page info */
#info-box {
  text-align: center;
  padding: 60px 0px;
}
#info-box > h2 {
  text-align: center;
  font-family: LL Circular, sans-serif;
  font-size: 1.375rem;
  line-height: 2rem;
  font-weight: 500;
  letter-spacing: -0.2px;
  padding: 22px;
}
.info-card {
  width: 140px;
  margin: 0px 7px;
}
.info-card > svg {
  margin: 10px;
}
.info-card > h4 {
  padding: 0px 5px;
  font-weight: 500;
  line-height: 1.2rem;
}
/* Big layout */
.big-layout {
  position: relative;
  margin-bottom: 110px;
}
.circle {
  z-index: -1;
  height: 750px;
  width: 750px;
  border-radius: 50%;
  background-color: var(--yellow);
  position: absolute;
  top: 50px;
  left: -200px;
}
.unipadding {
  content: "";
  margin: 0% 11%;
  clear: both;
  display: table;
  overflow: auto;
}
img {
  width: 100%;
  height: 100%;
}
h2 {
  font-family: "LL Circular", sans-serif;
  font-size: 2.5rem;
  line-height: 3.375rem;
  font-weight: 600;
  text-align: left;
  letter-spacing: -1.4px;
}
.big-layout h2 {
  margin-top: 100px;
}
p {
  font-family: Dutch801, serif;
  font-size: 1.125rem;
  line-height: 1.75rem;
}
.div50 {
  width: 50%;
  float: left;
  margin-top: 100px;
}
.text-assign {
  margin-left: 120px;
}
.text-assign h2 {
  margin-top: 80px;
  margin-bottom: 24px;
}
.text-assign p {
  margin: 0px;
  padding: 10px 0px;
}
a {
  text-decoration: none;
  font-family: "LL Circular", sans-serif;
  font-size: 1.125rem;
  line-height: 1.75rem;
  font-weight: 600;
  letter-spacing: 0px;
}
.text-assign div {
  margin: 20px 0px;
}
.text-assign a {
  cursor: pointer;
  text-align: left;
  margin: 0px;
  padding-bottom: 5px;
  border-bottom: 2px solid rgba(20, 43, 111);
  transition: opacity 200ms ease-in-out 0s;
}
.text-assign a:hover {
  opacity: 0.7;
  border-bottom: 2px solid rgba(20, 43, 111, 0.8);
}
.text-assign .label {
  background-color: var(--yellow);
  position: static;
}
/* 2nd info  */
.text-assign2 {
  margin-right: 140px;
}
.text-assign2 h2 {
  margin-bottom: 24px;
  margin-top: 80px;
}
.text-assign2 p {
  margin: 0px;
  padding: 10px 0px;
}
.text-assign2 div {
  margin: 20px 0px;
}
.text-assign2 a {
  text-align: left;
  cursor: pointer;
  margin: 0px;
  padding-bottom: 5px;
  border-bottom: 2px solid rgba(20, 43, 111);
  transition: opacity 200ms ease-in-out 0s;
}
.text-assign2 a:hover {
  opacity: 0.7;
  border-bottom: 2px solid rgba(20, 43, 111, 0.8);
}
/* 3rd info  */
#note-ref {
  font-weight: 300;
  opacity: 0.7;
  border-bottom: 1px solid rgba(255, 255, 255, 0);
}
.text-assign ul {
  margin-top: 20px;
}
.text-assign ul li {
  list-style: none;
  display: flex;
  align-items: center;
  text-align: left;
  margin: 0px -40px;
}
.m-text {
  margin: 0px 10px 5px 15px !important;
}
/* Skeptics Speak */
.speaker {
  padding: 100px 0px;
  /* z-index: 2; */
  background: rgba(242, 241, 245, 0.56);
  text-align: center;
  position: relative;
}
.c-pos {
  position: absolute;
  bottom: -350px;
  right: 0px;
  overflow: hidden;
  height: 700px;
  width: 350px;
}
.circle2 {
  height: 700px;
  width: 700px;
  border-radius: 50%;
  background-color: var(--yellow);
  position: absolute;
  top: 0px;
  z-index: -1;
}
.speaker h2 {
  text-align: center;
  padding: 20px;
}
.speaker-gallery {
  margin: 50px 0px 80px 0px;
  width: 100%;
  position: relative;
  text-align: left;
  display: flex;
  align-items: right;
  justify-content: right;
}
.speaker-gallery h2 {
  text-align: left;
}
.speaker-gallery p {
  padding-left: 20px;
}
.pos-rel {
  /* z-index: 0; */
  position: relative;
  top: 50px;
  left: 5%;
  font-size: 2.125rem;
  line-height: 2.5rem;
  width: 400px;
  height: auto;
}
.img-dis {
  width: 375px;
  height: 450px;
  margin: 0px 15px;
  cursor: pointer;
}
.img-dis2 {
  width: 290px;
  height: 350px;
  margin: 0px 15px;
}
#ref-logo {
  border-top: 4px solid var(--blue);
  width: 780px;
  display: inline-block;
}
#ref-logo > div {
  padding: 60px 0px 0px 0px;
  display: flex;
  justify-content: space-evenly;
}
#ref-logo svg {
  height: 40px;
  width: auto;
  margin: 0px 35px;
}
#ref-logo svg:hover {
  opacity: 0.7;
}
/* Make Your Self */
.maker {
  display: block;
  justify-content: center;
  text-align: center;
  margin: 100px 0px 50px 0px;
}
.maker > h2 {
  display: flex;
  justify-content: center;
  margin: 20px;
}
.maker a {
  text-decoration-line: underline;
  text-underline-offset: 5px;
}
.gallery-container {
  display: flex;
  margin-bottom: 100px;
  overflow: hidden;
}
.gallery {
  margin: 0px 5px;
}
hr {
  opacity: 0.5;
}
/* subscribe */
.div50 h3 {
  font-size: 1.625rem;
  line-height: 2.25rem;
  letter-spacing: -0.3px;
  margin: 0px;
  padding-right: 300px;
}
input {
  font-family: "LL Circular", sans-serif;
  background: rgb(242, 241, 245);
  border: 0px;
  padding: 20px;
  font-weight: 300;
  font-size: 1rem;
  line-height: 1;
  overflow: visible;
  float: left;
  height: 60px;
}
input[type="text"] {
  outline: none;
  border: 0px;
  width: 80%;
}
input[type="submit"] {
  width: 20%;
  cursor: pointer;
  text-decoration-line: underline;
  text-underline-offset: 7px;
}
input[type="submit"]:hover {
  color: rgb(20, 43, 111, 0.7);
}
.icons {
  padding-top: 20px;
  clear: both;
}
.icons ul {
  margin: 0px 0px;
  padding: 18px 0px 0px 0px;
}
.icons ul li {
  list-style: none;
  margin-right: 25px;
  display: inline-block;
}
.icons ul li a {
  cursor: pointer;
  transition: opacity 200ms ease-in-out 0s;
}
.icons ul li a:hover {
  opacity: 0.7;
}
/* important links  */
.imp-links {
  margin: 100px 11% 25px 11%;
  padding: 30px 0px;
  border-top: 4px solid var(--blue);
  border-bottom: 1px solid var(--blue);
  display: flex;
  justify-content: center;
}
.div25 {
  width: 25%;
  text-align: left;
}
.imp-links svg {
  margin: 20px 5px;
}
.imp-links ul {
  margin: 0px 0px;
  padding: 18px 0px;
}
.imp-links ul li {
  list-style: none;
  margin: 13px 0px;
  font-size: 12px;
  color: rgb(20, 43, 111, 0.5);
  font-weight: 550;
}
.imp-links ul li a {
  cursor: pointer;
  font-size: 16px;
  font-weight: 550;
  transition: opacity 200ms ease-in-out 0s;
}
.imp-links ul li a:hover {
  opacity: 0.7;
}
/* footer  */
footer {
  padding-bottom: 60px;
}
footer > .div50 {
  margin: 0px;
}
footer svg {
  margin: 0px 5px;
}
.footer-link {
  float: right;
  width: 90%;
}
footer,
footer a,
.footer-link a,
.footer-link p {
  margin-right: 20px;
  text-align: left;
  line-height: 20px;
  font-weight: 600;
  font-size: 11px;
  word-spacing: 0.4px;
}
.footer-border {
  border: 0.5px solid rgb(200, 200, 200);
  padding: 15px;
  margin-bottom: 15px;
}
.learn-more {
  margin-right: 0px !important;
  border-bottom: 1px solid var(--blue);
}
footer a:hover {
  opacity: 0.7;
}
